<template>
    <div class="other">
        <van-nav-bar
                title="个人资料"
                fixed
                left-text="返回"
                left-arrow
                @click-left="$router.back()"
        />
        <van-tabs>
            <van-tab title="基本资料">
                <van-cell title="头像">
                    <!-- 使用 right-icon 插槽来自定义右侧图标 -->
                    <div
                            slot="right-icon"
                            style="position: relative;width: 6rem;height: 6rem;overflow: hidden;border-radius: 50%;border: .1rem solid #f6f6f6;cursor: pointer;"
                    >
                        <Avatar style="width: 100%;height: 100%;object-fit: cover" :src="avatar" icon="ios-person" size="large" />
                        <input style="position:absolute;top: 0;right: 0;width: 100%;height: 100%;opacity: 0" type="file" @change="selectFile" />
                    </div>
                </van-cell>
                <van-field :value="userInfo.phone" disabled label="手机号" />
                <van-field maxlength="20" placeholder="请设置用户名" v-model="username" label="用户名" />
                <van-radio-group v-model="gender">
                    <van-cell-group>
                        <van-cell title="男" clickable @click="gender = 1">
                            <van-radio slot="right-icon" :name="1" />
                        </van-cell>
                        <van-cell title="女" clickable @click="gender = 0">
                            <van-radio slot="right-icon" :name="0" />
                        </van-cell>
                    </van-cell-group>
                </van-radio-group>
                <div style="padding: 2rem;"><van-button color="linear-gradient(to right, #4bb0ff, #6149f6)" style="width: 100%;" @click="saveUserInfo">保存信息</van-button></div>
            </van-tab>
            <van-tab title="密码修改">
                <van-field placeholder="请输入旧密码" style="margin-top: 1rem;" v-model="oldPassword" type="password" label="旧密码" />
                <van-field placeholder="请输入新密码" style="margin-top: 1rem;" v-model="newPassword" type="password" label="新密码" />
                <div style="padding: 2rem;"><van-button color="linear-gradient(to right, #4bb0ff, #6149f6)" style="width: 100%;" @click="reviseUserPwd">修改密码</van-button></div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
    import {mapState} from "vuex";
    import config from "../../../config";
    import {Toast} from 'vant'
    import {reviseUserPwd, saveUserInfo, uploadFile} from "../../../api";

    export default {
        name: "Setting",
        data(){
            return{
                username: '',
                avatar: '',
                gender: 1,
                oldPassword: '',
                newPassword: '',
            }
        },
        computed:{
            ...mapState(['userInfo'])
        },
        mounted() {
            if(config.getCache('userId')){
                let timer = null;
                timer = setInterval(()=>{
                    if(this.userInfo.id){
                        this.avatar = this.userInfo.avatar;
                        this.username = this.userInfo.username;
                        this.gender = Number(this.userInfo.gender);
                        clearInterval(timer);
                    }
                },10);
            }else{
                this.$router.replace('/user')
            }
        },
        methods:{
            async saveUserInfo(){
                let {avatar, username, gender} = this;
                if(!avatar){
                    return Toast('请上传你的头像')
                }
                if(!username){
                    return Toast('请设置你的用户名')
                }
                let data = await saveUserInfo({
                    avatar, username, gender, id: config.getCache('userId')
                });
                if (data.code === 200) {
                    Toast(data.data);
                    this.$store.dispatch('addUserInfo', {userInfo: data.result.userInfo});
                } else {
                    Toast(data.data);
                }
            },
            async reviseUserPwd(){
                let {oldPassword, newPassword} = this;
                if(!oldPassword || !newPassword){
                    return Toast('请填写所有字段');
                }
                // eslint-disable-next-line no-console
                console.log(oldPassword, newPassword);
                let data = await reviseUserPwd({
                    oldPassword, newPassword, id: config.getCache('userId')
                });
                if(data.code === 200){
                    Toast(data.data);
                    config.delCache('userId');
                    this.$store.dispatch('addUserInfo', {userInfo: {}});
                    this.$router.replace('/user/login');
                }else{
                    Toast(data.data);
                }
            },
            async selectFile(e){
                let file = e.target.files[0];
                if (file.type !== 'image/jpeg' && file.type !== 'image/jpg' && file.type !== 'image/png' && file.type !== 'image/gif') {
                    return Toast('您上传的文件：' + file.name + '格式不是png/jpg/jpeg/gif格式！请重新选择！');
                }
                if (file.size > 2097152 * 5) {
                    return Toast('您上传的文件：' + file.name + '大小超过10M！请重新选择！');
                }
                let formData = new FormData();
                formData.append('file', file);
                let res = await uploadFile(formData);
                if(res.code === 200){
                    this.avatar = res.result.url;
                }else {
                    Toast('上传失败')
                }
            },
        }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">

</style>
